import React, { Component } from 'react';
import './Bootstrap.min.css';
import './App.css';
import loading from './loading.svg';
import axios from 'axios';
import LatestMovies from './LatestMovies';
import SingleMovie from './SingleMovie';

class App extends Component {


  state = {
    movies: [
      
    ],
    page: 1,
    loading: true,
    short: 'download_count',
    order_by: 'desc',
    singlemovie: [
      {
        id : '',
        title : '',
        year : '',
        img : '',
        cat : [],
        imdb : '',
        large_cover_image : '',
        language : '',
        runtime : '',
        mpa_rating : '',
        description_full : '',
        cast : [],
        torrents : [],
        yt_trailer_code : ''
      }
    ],
    singledisplaying: false,
    query_term: '0',
    totolfound: 21,
    dataloaded: true
  }


  

  get_movies() {
    const apiurl = 'https://yts.am/api/v2/list_movies.json?sort_by=' + this.state.short +'&page=' + this.state.page +'&order_by=' + this.state.order_by + '&query_term=' + this.state.query_term;

    axios.get(apiurl).then(response => {

      const jsonreply = response.data;

      let mf = jsonreply.data.movies.map(item => ({
        id : item.id,
        title : item.title,
        year : item.year,
        img : item.medium_cover_image,
        cat : item.genres[0],
        imdb : item.rating
      }))

      this.setState({
        movies: mf,
        page: this.state.page + 1,
        loading: false
      })

      window.scrollTo(0, 0)



    }).catch(error => {
      this.setState({
        dataloaded: false,
        loading: false,
        movies: []
      })
    })
  }
  

  loadMoviesPages = (e) => {
    this.get_movies()
    this.setState({
      loading: true
    })
  }
  

  shortTitle = (e) => {
    this.setState({ short: 'title', page: 1, totolfound: 21 }, function () {
      this.get_movies()
      this.setState({
        loading: true
      })
    });
  }

  shortYear = (e) => {
    this.setState({ short: 'year', page: 1, totolfound: 21 }, function () {
      this.get_movies()
      this.setState({
        loading: true
      })
    });
  }

  shortRating = (e) => {
    this.setState({ short: 'rating', page: 1, totolfound: 21 }, function () {
      this.get_movies()
      this.setState({
        loading: true
      })
    });
  }

  shortPopularity = (e) => {
    this.setState({ short: 'download_count', page: 1, totolfound: 21 }, function () {
      this.get_movies()
      this.setState({
        loading: true
      })
    });
  }

  shortAscending = (e) => {
    this.setState({ order_by: 'asc', page: 1, totolfound: 21 }, function () {
      this.get_movies()
      this.setState({
        loading: true
      })
    });
  }

  shortDescending = (e) => {
    this.setState({ order_by: 'desc', page: 1, totolfound: 21 }, function () {
      this.get_movies()
      this.setState({
        loading: true
      })
    });
  }

  searchText = (e) => {
    this.setState({ query_term: e.target.value, page: 1 });
  }


  searchMovies = (e) => {
    e.preventDefault();
    this.setState({ page: 1 }, function () {
      this.get_movies()
      this.setState({
        loading: true,
        query_term: '0',
        totolfound: this.state.movies.length,
        dataloaded: true
      })
    });
  }



  componentDidMount() {
    this.get_movies()
    
  }

  details = (e) => {
    this.setState({ singlemovie: e }, function () {
      const sapiurl = 'https://yts.am/api/v2/movie_details.json?movie_id=' + this.state.singlemovie + '&with_images=true&with_cast=true';

      axios.get(sapiurl).then(response => {

        const sjsonreply = response.data;

        

        let mf2 = {
          id : sjsonreply.data.movie.id,
          title : sjsonreply.data.movie.title,
          year : sjsonreply.data.movie.year,
          img : sjsonreply.data.movie.medium_cover_image,
          cat : sjsonreply.data.movie.genres[0],
          imdb : sjsonreply.data.movie.rating,
          large_cover_image : sjsonreply.data.movie.large_cover_image,
          language : sjsonreply.data.movie.language,
          runtime : sjsonreply.data.movie.runtime,
          mpa_rating : sjsonreply.data.movie.mpa_rating,
          description_full : sjsonreply.data.movie.description_full,
          cast : sjsonreply.data.movie.cast,
          torrents : sjsonreply.data.movie.torrents,
          yt_trailer_code : sjsonreply.data.movie.yt_trailer_code
        }

        this.setState({
          singlemovie: mf2,
          singledisplaying: true
        })

        window.scrollTo(0, 0)


      }).catch(error => {
        this.setState({
          dataloaded: false,
          loading: false
        })
      })
    });
  }


  dismiswindow = (e) => {
    this.setState({
      singledisplaying: false
    })
  }
  


  render() {
    var loadbutton = '';
    var loadbuttontext = '';
    var loadbuttonclass = '';
    var shortwrappercls = '';
    var singledisplayingmarkup = '';
    if(this.state.loading === true) {
      loadbuttontext = 'Loading ...'
      loadbuttonclass = 'load-movies loading-yes'
      shortwrappercls = 'short-loading'

    } else {
      loadbuttontext = 'Page '+ this.state.page
      loadbuttonclass = 'load-movies'
      shortwrappercls = 'short-normal'
    }

    if(this.state.page > 1) {
      if(this.state.totolfound > 20) {
        loadbutton = <button className={loadbuttonclass} onClick={this.loadMoviesPages}> <img src={loading} alt="Loading"/>{loadbuttontext}</button>
      }
    }

    var headingtext = '';
    if(this.state.short === 'title') {
      headingtext = <h2>Movies by title</h2>
    }

    if(this.state.short === 'year') {
      headingtext = <h2>Movies by year</h2>
    }

    if(this.state.short === 'rating') {
      headingtext = <h2>Movies by rating</h2>
    }

    if(this.state.short === 'download_count') {
      headingtext = <h2>Movies by download count</h2>
    }

    if(this.state.singledisplaying === true) {
      singledisplayingmarkup = <SingleMovie dismiswindow={this.dismiswindow} info={this.state.singlemovie}/>
    }

    var movilistmarkup = '';
    if(this.state.dataloaded === true) {
      movilistmarkup = <LatestMovies details={this.details} movies={this.state.movies}/>
    } else {
      movilistmarkup = <div className="alert alert-danger text-center">Sorry nothing found!</div>
    }


    return (
      <div className="App">
        <div className="container">
          <div className="row">
            <div className="col">
              <header className="app-header">
              <div className="row">
                <div className="col-md-6 my-auto">
                {headingtext}
                <div className="short-more">
                  <div className={shortwrappercls}>
                    <button onClick={this.shortAscending} className={this.state.order_by}><img src={loading} alt="Loading"/> Ascending</button>
                    <button onClick={this.shortDescending} className={this.state.order_by}><img src={loading} alt="Loading"/> Descending</button>
                  </div>
                </div>
                </div>
                <div className="col-md-4 my-auto">
                  <div className="shorttext">
                    <p>Load again by</p>
                    <div className="short-buttons">
                      <div className={shortwrappercls}>
                        <button className={this.state.short} onClick={this.shortTitle}><img src={loading} alt="Loading"/> Title</button>
                        <button className={this.state.short} onClick={this.shortYear}><img src={loading} alt="Loading"/> Year</button>
                        <button className={this.state.short} onClick={this.shortRating}><img src={loading} alt="Loading"/> Rating</button>
                        <button className={this.state.short} onClick={this.shortPopularity}><img src={loading} alt="Loading"/> Popularity</button>
                      </div>
                    </div>
                  </div>
                </div>
                <div className="col-md-2 my-auto">
                  <p className="page-indicator-header">Page {this.state.page -1}</p>
                  <div className="movie-search">
                    <form onSubmit={this.searchMovies}>
                      <input onChange={this.searchText} ref="search" name="search" type="search" placeholder="Search movies"/>
                    </form>
                  </div>
                </div>
              </div>
              </header>
              {movilistmarkup}
              

              
              {loadbutton}
            </div>
          </div>
        </div>


        <div data-keyboard="false" className="modal fade" id="movie-info" tabIndex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
  <div className="modal-dialog" role="document">
    <div className="modal-content">
    <button onClick={this.dismiswindow} type="button" className="close" data-dismiss="modal" aria-label="Close">
          <span aria-hidden="true">&times;</span>
        </button>
      <div className="modal-body">
      {singledisplayingmarkup}
      </div>
    </div>
  </div>
</div>        
        
        
      </div>
    );
  }
}

export default App;
