import React, { Component } from 'react'
import loading from './loading.svg'
import imdb from './imdb.png'
import ReactWOW from 'react-wow'
export default class MoviesGrid extends Component {

    
  render() {
    

    return this.props.movies.map((item, index) => (
        
        <div key={item.id} className="col-lg-3 col-sm-6">
            <ReactWOW animation='fadeInUp' delay={index*100 + 'ms'} >
            <div className="single-movie-item">
                <div className="movie-poster">
                    <img className="movie-poster-img" src={item.img} alt={item.title}/>
                    <img className="loading-poster" src={loading} alt="loading ..."/>

                    <div className="movie-hover-info">
                    <p className="imdb-rating"><img src={imdb} alt="imdb-logo"/>{item.imdb} /10</p>
                    <p className="movie-genere">{item.cat}</p>
                    <button data-toggle="modal" data-target="#movie-info" onClick={() => this.props.details(item.id)}>View Details</button>
                    </div>
                </div>
                <h4>{item.title}</h4>
                <p className="movie-year">{item.year}</p>
            </div>
            </ReactWOW>
        </div>
    ))
  }
}
